<template>
    <div>  
        <el-row></el-row>
        <el-row >{{headerMessage}}</el-row>
        <el-form 
            label-width="90px" 
            label-position="left" 
            size="mini"
            :model="trlogin" 
            status-icon
            ref="trlogin"
            :rules="rules2" 
         >
        <el-row :gutter="2" >
            <el-col :span="26"  >
                <div >
                    <el-form-item label= "Card#" prop="cardNum" v-if="!this.$store.state.titleRequest.patronInfo.Authorized">
                        <el-input v-model.number="trlogin.cardNum"
                        autofocus="autofocus"
                         onfocus="this.value = this.value;">
                        pattern: </el-input>
                    </el-form-item>
                    <el-form-item label= "PIN#" prop="pinNum" v-if="!this.$store.state.titleRequest.patronInfo.Authorized">
                        <el-input v-model="trlogin.pinNum"></el-input>
                    </el-form-item>
                    <el-form-item  v-if="!this.$store.state.titleRequest.patronInfo.Authorized" 
                      >
                            <el-button type = "primary" @click="onSubmit('trlogin')" round>
                                Login</el-button> 
                               
                    </el-form-item>
                    <el-form-item v-if="this.$store.state.titleRequest.patronInfo.Authorized"> 
                            <el-button type = "primary" @click="onLogoutSubmit('trlogin')" round>
                                Logout</el-button> 
                                <!-- <span>{{buttonAuthMessage}}</span> -->
                    </el-form-item>
                </div>
                </el-col>
            </el-row>
        </el-form>
        <el-alert
        v-if="showAlert"
        @close="closedAlert"
        show-icon
        :title= alertTitle
        type="warning"
        :description = alertDescription
        >
      </el-alert>
    </div>
</template>

<script>

    import { mapGetters } from 'vuex'
	export default{ 
            data() {
                return {
                    alertTitle: 'Problem Authorizing Card and PIN',
                    alertDescription: 'Please verify credentials or get support at :' +
                     ' http://answers.douglascountylibraries.org/ ',
                     initialAlertDescription: 'Please verify credentials or get support at :' +
                     ' http://answers.douglascountylibraries.org/ ',
                    showAlert: false,
                    alertType: "warning",
                    buttonTitle: 'Login',
                    buttonAuthMessage: '',
                    trlogin: {
                        pinNum:'',
                         cardNum:'2302500',
                        
                    },
                    
                    sizeForm: {
                        name: '',
                    },
                    labelPosition: 'left',
                    rules2: {
                        pinNum: [
                        {required: true, message: 'Please input your pin Number', trigger: 'blur' },
                            { type: 'string', message: 'pin must be a number' },
                        ],
                        cardNum:[
                            {required: true, message: 'Please input Barcode Number', trigger: 'blur' },
                            {pattern: /(2302500)\d{7}/ || '^\\d{7}$' ||
                            /(230250)\d{6}-\d{1}/ , message: 'Barcode requires 14 digits' }
                        ]
                    }
                }; // end data return
         }, // end of data
      
        methods: {
            patronInfoGotMethod() {
                console.log(" patronInfoGotMethod",this.$store.getters.patronLoginGetter)
               // console.log(" patronInfoGot2",this.$store.titleRequest.getters.patronLoginGetter)
               // console.log(" patronInfoGot3",this.$store.getters.titleRequest.patronLoginGetter)
                     return this.$store.getters.patronLoginGetter;
                 },
           
            closedAlert()
            {
                this.showAlert = false;
            },
            onLogoutSubmit(formName)
            {
                console.log("logout",this.$refs[formName]);
                //this.$refs[formName].resetFields();
                console.log("pi", this.$patronData );
                this.$patronData.Authorized = false;
                this.$patronData.Barcode ="";
                this.$patronData.Pin = "";
                this.$patronData.Name = "Logging.Out";
                this.$patronData.Email = "";
                console.log("pi", this.$patronData );
                
                this.$store.commit('titleRequest/patronInfo', this.$patronData);
               // this.$store.commit('titleRequest/selectedTab', "Login");
                this.trlogin.cardNum = "2302500";
                this.trlogin.pinNum = "";
            },

            onSubmit(formName) {
                console.log("data",this.trlogin);
                console.log("formName",formName);
                
            
                this.$refs[formName].validate((valid) => {
                    console.log("valid", valid);
                    if (valid) {  
                        this.$titleRequestObject.Barcode = this.trlogin.cardNum;
                       // this.$myTitleRequest.Barcode = this.trlogin.cardNum;
                        this.$titleRequestObject.Title = 'test';
                       // this.$myTitleRequest.Title = 'test';
                        console.log("data", this.trlogin);
                        // this.$store.state.titleRequest.selectedRequest.Barcode = 777;
                        console.log("myTitleRequest", this.$titleRequestObject);
                        this.$store.dispatch('titleRequest/getPatron', {
                            id: this.trlogin.cardNum,
                            pin: this.trlogin.pinNum
                        }).
                            then(() => {
                                console.log("completed login request",
                                    this.$store.state.titleRequest.patronInfo);
                                if (this.$store.state.titleRequest.patronInfo.Authorized === true) {
                                    this.$store.commit('titleRequest/selectedTab', "SubmitRequest");
                                    console.log("completed login request with auth true",
                                        this.$store.state.selectedTab);
                                        this.showAlert = false;
                                        this.buttonTitle = 'Logout';
                                        this.buttonAuthMessage = 'Successfully Authorized';
                                        this.alertDescription = this.initialAlertDescription;
                                }
                                else{
                                    console.log("Failed authorize", this.$store.state.titleRequest.patronInfo);
                                    if (this.$store.state.titleRequest.patronInfo.Message.includes('Likely'))
                                    {
                                        this.alertDescription = this.initialAlertDescription + '.  This appears to be a connection problem. ';
                                    }
                                    this.showAlert = true;
                                }
                                ;
                                // this.dbCount = this.searchData.length;
                            });
                        //  this.$store.commit('titleRequest/selectedRequest');
                        console.log("dtr2ata", this.trlogin);
                        console.log("post requ3st", this.$store.state.titleRequest.patronInfo);
                    }
                    else {
                        console.log('error submit!!');
                        return false;
                    }
               });
            },
            
        }, // end methods
        components:{
			//CheckFilter
        },
		props:[
		
		],
		computed: {
            headerMessage(){
                var ret;
                if (this.$store.state.titleRequest.patronInfo.Authorized) {
                    ret = "You have already successfully logged in."
                }
                else{
                    ret = " Please Enter Your Library Card and PIN Number";
                }
                return ret;
            },
            ...mapGetters({
                pi: 'titleRequest/patronLoginGetter',
            })
        },
        created(){
            this.popToggleCustom = false;
            
            console.log('params', this.$route);
            // this.getParams();
   
    }
		
    }
</script>
<style>
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom:10px;
    }
  }

</style>


