<template>
    <div>
        <el-dialog title="" :visible.sync="articleDialogVisible" width="30%"
            :before-close="handleArticleDialogClose">
          <span>  <b> WARNING CONCERNING COPYRIGHT RESTRICTIONS</b> 
            <p>
               The copyright law of the United States (Title 17, United States Code) governs the
                making of photocopies or other reproductions of copyrighted material. 
                Under certain conditions specified in the law, 
                libraries and archives are authorized to furnish a photocopy or other reproduction.
                One of these specified conditions is that the photocopy or reproduction is
                not to be used for any purpose other than private study, scholarship,
                or research. If a user makes a request for, or later uses, 
                a photocopy or reproduction for purposes in excess of fair use, 
                that user may be liable for copyright infringement. 
                This institution reserves the right to refuse to accept a copying order if, 
                in its judgment, fulfillment of the order would involve violation of copyright law.
            </p>
            </span>
        
                <span slot="footer" class="dialog-footer">
                 <!-- <el-form-item label="Acknowledgment"> --> 
                                <el-radio-group v-model="articleRadio">
                                    <el-radio border label="Accept">Accept</el-radio>
                                    <span> or</span>
                                    <el-radio border label="Decline">Decline</el-radio>
                                  </el-radio-group>
                                 
                <!-- </el-form-item> -->
           </span>
        </el-dialog>
        <el-alert
            v-if="cdPurchaseAlertVisible"
            @close="handleCDNoticeClose"
            show-icon
            :title= "alertTitle"
            type="warning"
            :description= "alertDescription"
            >
          </el-alert>
    
    <div class="form-div">  
            <el-row></el-row>
            <el-row><h6>Make a request for a new title here.</h6></el-row>
            <div class="request-info">
                <el-row>
                    <el-col  :xs="6" :sm="6" :md="10" :lg="11" :xl="11">
                    If you can’t find a title in our catalog, you can request it using this form.
                    Enter as much information as you have, including title, ISBN, season number, and publication date.
                    </el-col>
                 </el-row>
             </div>
            <el-row> <div class="request-submit"> 
                 Please only submit 1 print title or 1 TV season per form 
            </div>
        </el-row>
            
      
         <el-form 
         label-width="120px" 
         label-position="left" 
         size="mini"
         :rules="rules" 
         :model="ruleForm"
         ref="ruleForm"
         >
            <el-row :gutter="4">
                <el-col :span="8">
                    <div>
                        <el-form-item label="Format">
                            <div>
                            <el-select v-model="ruleForm.Format" 
                                        placeholder="Pick a Format"
                                        @change="formatChange"
                                        prop="Format" 
                                       >
                                       <el-option v-for="item in formats" :label="item.name"
                                       :value="item.id" :key="item.id">
                                      </el-option>
                            </el-select>
                        </div>
                        </el-form-item>
                        
                        <el-form-item label="Title" v-if="ruleForm.Format!==9"  prop="Title" >
                            <el-input v-model="ruleForm.Title"></el-input>
                        </el-form-item>
                        <el-form-item label="Artist/Composer"  prop="Author" v-if="ruleForm.Format===5">
                            <el-input v-model="ruleForm.Author"></el-input>
                        </el-form-item>
                        <el-form-item label="Season" v-if="ruleForm.Format===3"  prop="Season">
                            <el-input v-model="ruleForm.Season"></el-input>
                        </el-form-item>
                        <el-form-item label="Actor / Director" 
                                 prop="Author" v-if="ruleForm.Format===3">
                            <el-input v-model="ruleForm.Author"></el-input>
                        </el-form-item>
                        <el-form-item label="Author"  prop="Author"
                                    v-if="!(ruleForm.Format ===3 || ruleForm.Format ===5 )">
                            <el-input v-model="ruleForm.Author"></el-input>
                        </el-form-item>
                        <el-form-item label="ISBN"  prop="Isbn" v-if="ruleForm.Format!==9">
                            <el-input v-model="ruleForm.Isbn"></el-input>
                        </el-form-item>
                        <el-form-item label="UPC" prop="Upc" 
                        v-if="(ruleForm.Format ===3 || ruleForm.Format ===5  || ruleForm.Format ===10)">
                            <el-input v-model="ruleForm.Upc"></el-input>
                        </el-form-item>
                        <el-form-item label="OCLC Number" prop="OclcNumber"  v-if="ruleForm.Format!==9">
                            <el-input v-model="ruleForm.OclcNumber"></el-input>
                        </el-form-item>
                        
                    <!--    Article         -->
                        <el-form-item label="Magazine / Journal Title" prop="MagazineTitle"
                             v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.MagazineTitle"></el-input>
                        </el-form-item>
                        <el-form-item label="Article Title" prop="ArticleInfo"
                             v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.ArticleInfo"></el-input>
                        </el-form-item>
                        <el-form-item label="Date"  prop="MagazineDate"
                                v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.MagazineDate"></el-input>
                        </el-form-item>
                        <el-form-item label="Volume" prop="MagazineVolume"
                                v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.MagazineVolume"></el-input>
                        </el-form-item>
                        <el-form-item label="Number" prop="MagazineNumber" 
                            v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.MagazineNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="Page Numbers" prop="MagazinePageNumbers"
                            v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.MagazinePageNumbers"></el-input>
                        </el-form-item>
                        <el-form-item label="ISSN" prop="Issn" v-if="ruleForm.Format===9">
                            <el-input v-model="ruleForm.Issn"></el-input>
                        </el-form-item>
                       <!--    End Article         -->

                        <el-form-item label="Publisher" prop="Publisher"
                             v-if="ruleForm.Format!==9">
                            <el-input v-model="ruleForm.Publisher"></el-input>
                        </el-form-item>
                        <el-form-item label="Publication Year" prop="PublicationYear" 
                                v-if="ruleForm.Format!==9">
                            <el-input v-model="ruleForm.PublicationYear"></el-input>
                        </el-form-item>
                        <span v-if="ruleForm.PatronEmail ==''" >Please enter email</span>
                        <!-- <span v-if="!checkIfEmail" >Please enter email</span> -->
                        <el-form-item label="Email" prop="PatronEmail">
                            <el-input v-model="ruleForm.PatronEmail"></el-input>
                        </el-form-item>
                        <el-form-item label="Pickup Location" prop="HoldPickupLocation">
                            <el-select v-model="ruleForm.HoldPickupLocation" 
                                    placeholder="Pick a Location"
                                    @change="locationChange">
                                    <el-option v-for="item in locations" :label="item.name" 
                                    :value="item.id" :key="item.id" >
                                    </el-option>
                            </el-select>
                        </el-form-item>
                        
                        We would love to know how you heard about this title:
                        <el-input type="textarea"
                        :rows="5"
                        v-model="ruleForm.PatronComments" prop="PatronComments"></el-input>
                        <el-row></el-row> 
                        <el-form-item>
                            <el-button type='primary' @click="onSubmit"
                               round :disabled="submitDisabled">{{submitLabel}}
                            </el-button>
                        <span v-show="false">{{tabselected}}</span>
                        <span v-show="false">{{forceUpdate}}</span>
                        </el-form-item>
                    </div>
                </el-col>
            </el-row>
        </el-form>
    </div>
</div>
</template>

<script>
	//import genres from '../util/genres';
    //import CheckFilter from './CheckFilter.vue';
    import RequestHelperMixin from '@/mixins/requestHelper';
    import { mapGetters } from 'vuex';
	export default{ 
            data() {
                return {
                    forceUpdate:"",
                    articleDialogVisible:false,
                    cdPurchaseAlertVisible:false,
                    titleRequestData:{},
                    submitDisabled:false,
                    alertTitle: 'No Requests Checked',
                    alertDescription: 'Please use check box to indicate requests to update.',
                    showAlert: false,
                    showedProspector: false,
                    alertType: "warning",
                    selectedArticleRadio:"",
                    localStatus: 1000,
                    localFormat: 0,
                    popToggleCustom: true,
                    emailSentDialogVisible: false,
                    sizeForm: {
                        name: '',
                    },
                    labelPosition: 'right',
                    email:'',
                    location:'',
                    ruleForm: this.$titleRequestObject,
                    ruleForm2: {
                        patronEmail: '',
                        location: '',
                        format: '',
                        date2: '', 
                        delivery: false,
                        type: [],
                        resource: '',
                        desc: ''
                    },
                 rules: {
                    Format: [
                            { required: true, message: 'Please select the title format', trigger: 'blur' },
                           // { min: 3, max: 5, message: 'Length should be 3 to 5', trigger: 'blur' }
                        ],
                        Title: [
                            { required: true, message: 'Please enter Title', trigger: 'blur' },
                           // { min: 1,  message: 'Enter title', trigger: 'blur' }
                       
                        ],
                        Author: [
                            { required: true, message: 'Please enter Author', trigger: 'blur' },
                            { min: 1,  message: 'Enter Author', trigger: 'blur' }
                       
                        ],
                        ArticleInfo: [
                            {  required: true, message: 'Please enter article title', trigger: 'blur' }
                        ],
                        MagazineDate: [
                            {  required: true, message: 'Please enter Magizine date', trigger: 'blur' }
                        ],
                        MagazineVolume: [
                            {  required: true, message: 'Please enter the magazine volume', trigger: 'blur' }
                        ],
                        MagazineTitle: [
                            {  required: true, message: 'Please enter the magazine title', trigger: 'blur' }
                        ],
                        MagazinePageNumbers: [
                            { required: true, message: 'Please enter the magazine page numbers', trigger: 'blur' }
                        ],
                        MagazineNumber: [
                            { required: true, message: 'Please enter the magazine number', trigger: 'blur' }
                        ]
                 }
                   // selectedOptions: ['Pending','100'],
                };

         }, // end of data
      
        methods: {
            cancelArticleDialog() {
               // handleCDNoticeClose(done) {
                this.cdPurchaseAlertVisible = false;
                // this.$confirm('Are you sure to close this dialog?')
                //     .then(_ => {
                //         done();
                //     })
                //     .catch(_ => { });
            },
            formatChange(event)
            {
                console.log("format change", event);
               // this.$refs["ruleForm"].clearValidate();
                this.ruleForm.Format = event;
                this.forceUpdate=event;
                //console.log("forceUpdate", this.forceUpdate);
              //  this.$refs["ruleForm"].resetFields();
                if (event === 5) {
                   // console.log("event === 5", event);
                    this.openCDAlert();
                }
                if (event === 9) {
                   // console.log("event === 9", event);
                   if (this.selectedArticleRadio !== "Accept") {
                    this.articleDialogVisible = true;
                   }
                  
                }
                if (!(event === 6 || event == 7)) {
                   // console.log("!event === 6,7", event);
                    if ( this.showedProspector === false) {
                        this.openProspector();
                        this.showedProspector = true;
                    }
                }
                console.log("end format change", event);
                
            },
           locationChange(event)
            {
                console.log("Location change", event);
                this.ruleForm.HoldPickupLocation = event;
                this.forceUpdate=event;
                if (event !== this.location) {
                     this.changeLocationAlert();
                }
              //  this.$refs["ruleForm"].resetFields();
                
            },
           changeLocationAlert() {
                this.$alert('You are changing your Pickup Location' +
                ' from the setting in your account profile,'
               );
             },
            openCopyRightAlert() {
                this.$alert(
                '<strong> Acceptance of the Copyright is required. </strong>' + 
                ' To continue requesting an article, choose the Article format and then accept the Copyright.' +
                '<i> The current request format is reset. </i>' ,
                {
                    dangerouslyUseHTMLString: true
                  });
             },
            openCDAlert() {
                this.$alert(
                '<strong> Douglas County Libraries is no longer purchasing music CDs.' + 
                '<i> Your request will be forwarded to our Interlibrary Loan Department.</i>' +
                ' You can also access digital music on our website using  <a target="_blank" href="http://douglascountylibraries.freegalmusic.com/homes/index">Freegal</a>' +
                ' and <a target="_blank" href="https://www.hoopladigital.com/home">Hoopla</a>.</strong>', 
                'Music CDs are now Interlibrary Loan Items', 
                  {
                    dangerouslyUseHTMLString: true
                  });
             },
             handleArticleDialogClose(done) {
                this.articleDialogVisible = false;
                console.log("done",done);
                // this.$confirm('Are you sure to close this dialog?')
                //     .then(_ => {
                //         done();
                //     })
                //     .catch(_ => { });
            },
            popoverCustom(event) {
                //console.log('clicked popover 5', event);
                console.log('clicked popover custom', this.popToggleCustom, ' ', event);
                if (this.popToggleCustom) {
                    this.popToggleCustom = true;
                } else {
                    this.popToggleCustom = true;
                    // this.FilterState = "Hide Filters";
                }
        }, 
       
            onSubmit() {
                console.log("this.ruleForm.Format  ", this.ruleForm.Format,
                "this.selectedArticleRadio ",this.selectedArticleRadio
            );
                if (this.ruleForm.Format === 9 && this.selectedArticleRadio == "Decline") {
                    this.openCopyRightAlert(); 
                }
                else{
               
                this.$refs["ruleForm"].validate((valid) => {
                    // this.$refs[formName].validate((valid) => {
                    console.log("valid", valid);
                    if (valid) {
                        this.titleRequestData.PatronEmail = this.email;
                        console.log("titleRequest data", this.titleRequestData);
                        console.log("ruleForm valid", this.ruleForm);
                       // convert the format back to digit
                       console.log("HoldPickupLocation ", this.ruleForm.HoldPickupLocation );
                       var data = this.ruleForm;
                        //  data.HoldPickupLocation = this.$locations2.find(x => x.name === 
                        // this.ruleForm.HoldPickupLocation).id;
                       data.LastFormat = this.ruleForm.Format;
                       data.Status = 1;
                       data.LastStatus = 1;
                       data.ProcessStatus = 100;
                       data.EmailTemplateSent = 500;
                       data.LastEmailTemplateSent = 500;
                      // data.Format = 1;
                        this.$store.dispatch('titleRequest/saveTitleRequest',data);
                        console.log("ruleForm valid data", data);
                        this.ruleForm =  {};
                        this.transferPatronInfo();
                        this.ruleForm.Format = data.Format;
                        this.openSavedRequest();
                        //this.$store.commit('titleRequest/selectedChange', false);
                        //   this.$store.dispatch('titleRequest/saveTitleRequest');
                    } else {
                        console.log("ruleForm INvalid", this.ruleForm);
                    }
                });
                 }
            },
            transferPatronInfo(){
                var patronInfo = this.getPatronInfo;
                console.log("transferPatronInfo patroninfo",patronInfo)
                console.log("transferPatronInfo patroninfo.Location",patronInfo.Location)
                console.log("this.$locations2",this.$locations2);
               // console.log("titleRequestData patron",this.$store.state.titleRequest.patronInfo)
              
                // this.titleRequestData.Title = this.titleRequestData.Title;
               var locationName = this.$locations2.find(x => x.HorizonName === 
                  patronInfo.Location).id;
                  this.location = locationName;
                  console.log("locationName",locationName);
               this.ruleForm.HoldPickupLocation = locationName;
              // this.titleRequestData.PatronEmail = patronInfo.Email;
              this.ruleForm.CreatedBy = patronInfo.Pin;
              this.ruleForm.Barcode = patronInfo.Barcode;
              this.ruleForm.UserName = patronInfo.Name;
                this.ruleForm.PatronEmail = patronInfo.Email;
                console.log("transferPatronInfoend patroninfo",this.titleRequestData)
                console.log("transferPatronInfoend patroninfo",this.ruleForm)
            },
             
            
        }, // end methods
        components:{
			//CheckFilter
        },
		props:[
		
		],
		computed:{
            articleRadio:{
                get() {
                   
                    console.log("articleRadio  selected arr", this.selectedArticleRadio);
                    return this.selectedArticleRadio;
                },
                set(value) {
                  console.log("articleRadio set", value);
                  if (value === "Decline") {
                    this.openCopyRightAlert(); 
                    this.ruleForm.Format = "";
                  }
                  this.articleDialogVisible = false;
                  this.selectedArticleRadio = value;
                  
                }
            },
            checkIfEmail(){
                var ret = true;
                if (this.ruleForm.PatronEmail === null ||
                this.ruleForm.PatronEmail ==="" ) {
                  ret =  false ; 
                }
                return ret;
            },
            tabselected(){
                var x = this.$store.state.titleRequest.selectedTab;
                console.log('PATRON FORM  tabselected ',  this.$store.state.titleRequest.selectedTab);
                console.log('PATRON Data   ',  this.$store.state.titleRequest.patronInfo);
                if (this.$patronData.Name !== "Logging.Out") {
                     this.transferPatronInfo();
                 }
                // else{
                //     this.$patronData = {};
                // }
               
                return x;
            },
            submitLabel()
            {
                var label;
                  if (this.$store.state.titleRequest.patronInfo.Authorized ===false)
                 {
                    label = 'Please Login First';
                    this.submitDisabled = true;
                    this.$store.commit('titleRequest/selectedTab', "Login");
                    console.log('patron form auth false ',  this.$store.state.titleRequest.patronInfo.Authorized);
                }
                else{
                    this.submitDisabled = false;
                    
                    label = 'Submit Request'
                }
                return label;
            },
          
            // getSelectedRequest()
            // {
            //    // console.log('getSelectedRequesto ', this.$store.state.titleRequest.selectedRequest);
            //         return this.$store.state.titleRequest.selectedRequest;
            // },
            ...mapGetters({
               getPatronInfo: 'titleRequest/patronLoginGetter',
            }),
           
           
            titleRequest(){
               
                console.log("titleRequest Data patron",this.titleRequestData)
                return this.titleRequestData;
               },

           
            formats(){
               // return this.$store.getters['titleRequest/format'];
               // return this.$store.state.titleRequest.requestConfigs.Status;
               console.log('  Formats',this.$formats);
               return this.$formats;
            },
            locations(){
               // return this.$store.getters['titleRequest/format'];
               // return this.$store.state.titleRequest.requestConfigs.Status;
            
               console.log('  locations',this.$locations);
               return this.$locations;
            },
  
        },
        mixins: [
        RequestHelperMixin,
    ],
        created(){
            this.popToggleCustom = false;
            console.log('patron form created ',  this.$store.state.titleRequest.patronInfo.Authorized);
           // this.transferPatronInfo();
          
            //this.titleRequest.Email = this.$store.state.patronInfo.Email;
           // this.formatSelected = this.$store.state.titleRequest.selectedRequest.Format;
            //console.log('create formatselected', this.$formatSelected);
            //console.log('email', this.titleRequest.patronInfo.Email);
            // this.getParams();
    }
    }
</script>
<style>
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom:10px;
    }
  }
  .form-div {
    border-radius: 14px;
    padding: 0px 0;
    margin:0;
    width:1200px;
  }
  .el-col {
    border-radius: 14px;
    padding: 0px 0;
  }
  
  .request-info {
    background: #f2f3ee;
    font-size:.9em;
    margin-top:-1em;
  }
  .request-submit {
    -webkit-text-fill-color:rgb(155, 18, 45);
    font-size:.9em;
    margin-top:-.5em;
    font-weight: 600;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }

  .row-bg {
    padding: 0px 0;
    background-color: #f9fafc;
  }
 
/*
.el-select-dropdown__list  {
    height: 300px;
  }
  .el-scrollbar  {
    height: 300px;
  }
*/
 .el-form {
    background: #f7f7f3;
    margin-top:0px;
    margin-right:10px;
    margin-left:0px;
    top: 1px;
    padding:10px;
    font-size:1em;
    width: 100%;
    border: 0px solid #666;
    text-align: left;
} 
.el-item {
    left: 1px;
    top: 1px;
    font-size:10em;
    text-align: left;
}

.el-input__inner{
    padding-bottom: 2px;
    font-size:1.3em;
    margin-top:1px;
    font-weight: 300
}
.el-form-item__error{
    margin-top: -.7em;
}
.el-form-item__label{
    padding-bottom: 2px;
    font-size:.9em;
    margin-top:1px;
    font-weight: 500
}
.el-form-item-first{
    padding-bottom: 2px;
    margin-top:1em;
    margin-top: 10em;
}
.el-form-item-mini{
    background: rgb(194, 12, 12);
    padding-bottom: 2px;
    margin-top:1px;
}
.el-form-item{
    margin-right:10px;
    margin-bottom: -.2em;
    margin-top: -.7em;
    padding: 0 ;
}
.el-select-dropdown__item{
    margin-right:1px;
    margin-bottom:-.8em;;
    margin-top: 0;
    padding: 0 ;
    /* &:first-child {
        margin-top:100px;
    } */
}
/* .el-select{
    height: 100px ;
} */
/* .el-select-dropdown__wrap el-scrollbar__wrap{
    height: 500px ;
}
.popper{
    height: 100px ;
} */
</style>


