<template>
    <!-- <el-alert
        v-if="showAlert"
        @close="closedAlert"
        show-icon
        :title= alertTitle
        type="warning"
        :description = alertDescription
        >
    </el-alert> -->
    <div class="mainTab">
           
        <el-tabs type="border-card" v-model="activeName" @tab-click="handleTabClick">
             <el-tab-pane label=Login v-if="!this.$store.state.titleRequest.patronInfo.Authorized" name="Login">
                <div>
                    <login></login>
                </div>
            </el-tab-pane>
            <el-tab-pane label=Logout v-if="this.$store.state.titleRequest.patronInfo.Authorized" name="Login">
                    <div>
                        <login></login>
                    </div>
            </el-tab-pane>
            <el-tab-pane label="Submit New Request" name="SubmitRequest" >
                    <div>
                            <patron-form-main></patron-form-main>
                        </div>
            </el-tab-pane>
            <el-tab-pane label="Show My Requests" name="ShowRequests">
                <div>
                        <patron-requests></patron-requests>
                    </div>
                   
        </el-tab-pane>
              
          
        </el-tabs>
    </div>
</template>


<script>
    import RequestHelperMixin from '@/mixins/requestHelper';
	import PatronFormMain from '@/components/PatronFormMain';
    import Login from '@/components/Login';
    import PatronRequests from '@/components/PatronRequests';
	export default{ 
        data(){
            return {
                popToggle: false,
                FilterState: "Show Filter",
                labelName: "Login",
            }
        },
       
        methods:{
            handleTabClick(tab, event) {
               // this.$store.commit('titleRequest/selectedTab', tab._props.label)
               //  this.$store.commit.titleRequest.selectedTab;
              // console.log('tab click', tab._props.label);
               // console.log('this.$store.commit.titleRequest.selectedTab', this.$store.state.titleRequest.selectedTab);
               if (this.$store.state.titleRequest.patronInfo.Authorized === false  &&
                   tab.name !== "Login")
                {
                   // this.$store.commit('titleRequest/selectedTab',"Login");
                    console.log(' get active Name  auth false ',  this.$store.state.titleRequest.patronInfo.Authorized);
                    this.openTabDiabledNotice(); 
                }
                if ( tab.name === "ShowRequests") {
               console.log('dispatching  get requests',tab, event);
                    
                this.$store.dispatch('titleRequest/getPatronRequests',this.$store.state.titleRequest.patronInfo.Barcode);
                    
                }
                
               console.log('handling event tab',tab, event);
               
              },
             
              openTabDiabledNotice() {
                const h = this.$createElement;
                this.$notify({
                    title: 'Please Login',
                    message: h('i', { style: 'color: maroon' }, 'Please Login in to Submit or View Request')
                });
             },
        },
        components:{
            Login,
             PatronFormMain,
             PatronRequests,
        },
		props:[
		
		],
		computed:{
            activeName: {
                get() {
                    console.log(' get active Name', this.$store.state.titleRequest.selectedTab);
                    var tab = this.$store.state.titleRequest.selectedTab;

                    return this.$store.state.titleRequest.selectedTab;
                },
                set(value) {
                    console.log('selectedTab set active Name', '-', value, '-');
                    this.$store.commit('titleRequest/selectedTab', value);
                    // if (value === 'SubmitRequest') {
                    //     this.activeName= 'Login';
                    //     this.$store.commit('titleRequest/selectedTab','Login');
                    // }

                    if (value === 'ACQPendingMultiGrid') {

                    }
                }
              },
             
              
              },
        
        mixins: [
        RequestHelperMixin,
    ],
        created(){
            console.log(' created MainTab', this.$store.state.titleRequest.selectedTab);
        }
		
		
    }
</script>
<style>
 
 div.el-tabs{
    padding-top:0;
   margin-top:-2em;
   min-height: 760px;
   
}
div.el-tabs__content{
    padding-top:0;
    margin-top:-2em;
    margin-left:-.8em;
   min-height: 760px;
  
}
.el-tabs__item{
    font-size: 1.05em;
    margin-top:2em;
    -webkit-text-fill-color: rgb( 0, 151,167);
    background:rgba(250, 249, 245, 0.801);
     /* background:#FFFFFF; */
   
}

.el-tabs__header .el-tabs__item.is-top.is-active{
    font-size: 1em;
    -webkit-text-fill-color: #FFFFFF;
    background-color: rgb( 0, 151,167);
    
    /* -webkit-text-fill-color: rgb( 0, 151,167);
    background: rgb(214, 246, 182);
    background: rgb( 0, 151,167); */
    /* background: #0097A7;
    color: #FFFFFF; */
   
    /* background: rgb(214, 246, 182);
    background: rgb( 0, 151,167);
      background: #0097A7;
    color: rgb(18, 6, 189); */
}
</style>


