<template>
        <div>
            <el-row></el-row>
            <el-row></el-row>
            
            <el-row class="steelGray"><h5>My Requests (Last 6 months)</h5></el-row>
        
              <el-table
                :data = gridRequests()
                :default-sort = "{prop: 'CreatedTime', order: 'descending'}"
                empty-text="No Data" 
                :row-class-name="tableRowClassName"
                :row-key="getRowKeys"
                ref="singleTable"
                height="750"
                stripe
                highlight-current-row 
               
                @row-click="handleRowClick"
                style="width: 100%" 
                >
                <h3 slot="empty">
                   No Requests Found
                </h3>
               
                <el-table-column prop="Title" label="Title"  sortable width="180">
                </el-table-column>
                <el-table-column prop="Author" label="Author" sortable width="180">
                </el-table-column>
                <!-- <el-table-column prop="Format" label="Format" sortable  :formatter="formatFormatter"width="120">
                </el-table-column> -->
                <el-table-column prop="Status" label="Status" sortable :formatter="statusFormatter" width="180">
                </el-table-column>
                 <el-table-column prop="CreatedTime" label="Created"  sortable :formatter="createdDateFormatter" width="180"></el-table-column>
               <el-table-column prop="EmailText" label="Completed Reason"  show-overflow-tooltip width="180"> </el-table-column>
               
              </el-table>
        </div>
                   
    </template>
    
    <script>
       import { mapGetters } from 'vuex';
       import RequestHelperMixin from '@/mixins/requestHelper';
        export default {
        data() {
              return {
                dialogVisible: false,
                lastSelected:[],
                idClicked:0,
                handledSelected:false,
                activeIndex: 0,
              }
            },
          computed:{
            // filteredRequests(){
            //         return this.$store.getters.getFilteredRequests;
            //     },
        
               
                setHandledSelected(value){
                    console.log("set handledSelected",value)
                    return this.handledSelected = value;
                },
                
                ...mapGetters({
                   // filteredRequests: 'titleRequest/getFilteredRequests',
               })
          
            },
            methods: {
              gridRequests(){
                console.log('tbab ',this.$store.state.titleRequest.selectedTab);
                if (this.$store.state.titleRequest.selectedTab == "ShowRequests") {
                  console.log("all requests",this.$store.state.titleRequest.patronRequests);
                    var array = this.$store.state.titleRequest.patronRequests;
                    console.log("grid array",array);
                     return array ;
                }
              },
              getRowKeys (row) {
               // console.log("getRowKeys",row)
                     return row.key
              },
              indexMethod (index) {
                console.log("indexMethod",index)
                    
              },
              handleClose(done) {
                    this.$confirm('Are you sure to close this dialog?')
                        .then(_ => {
                            done();
                        })
                        .catch(_ => { });
                },
                handleRowLeave(row, event, column) {
                   return 'warning-row';
               // return '';
                },
                handleRowClick(row, event, column) {
                  this.idClicked = row.Id;
                  console.log('row clicked request grid', row, ' ' ,column, ' ', event);
                  return 'warning-row';
               // return '';
                },
              
              // statusFormatter(row,column)
              // {
              //  // console.log('formateer row coloum ',row, ' ',column)
              //   var d = 
              //   this.$store.state.titleRequest.requestConfigs.Status.find(x => x.StatusId === row.LastStatus).Name;
              //   return  d ;
              //  // return row.CreatedTime +  'xxx';
              // },
            
              rowSelected({row, event, column}) {
                console.log('rowselected request grid', row, ' ' , event)
               // return '';
              },
    
             // tableRowClassName({row, rowIndex}) {
              tableRowClassName(row, rowIndex) {
               // console.log('tableRowClassName', row.row.Id, ' ' , this.$store.state.titleRequest.selectedRequest.Id)
              //  console.log('tableRowClassName', row, ' ' , this.$store.state.titleRequest.selectedRequest.Id)
                // console.log('tableRowClassName',rowIndex)
                // this.idClicked = row.Id;
                if (row.row.Id ===  this.idClicked) {
                  return 'warning-row';
                 } 
                 else{
                  return '';
                 }
                // // else if (rowIndex === 3) {
                // //   return 'success-row';
                // // }
            //     if (rowIndex === 1) {
            //   return 'warning-row';
            // } else if (rowIndex === 3) {
            //   return 'success-row';
            // }
           // return '';
               // return 'warning-row';
              },
           
    
              submitChange() {
                  var selectedReq = this.lastSelected;
                  this.dialogVisible = false;
                  console.log('submit! before this guy from grid--', selectedReq);
                  this.open(selectedReq);
                },
              // setCurrent(row) {
              //  // this.$refs.singleTable.setCurrentRow(row);
              //  console.log (" set selected row", row );
              //  console.log (" set selected row",this.$refs );
              //        this.$refs.singleTable.setCurrentRow(this.filteredRequests[0]);
              //        console.log (" set selected row", row )
              //       },
              handleSelectionChange(selection){
                      console.log ("xxxxxxxx handleSelectionChange",  selection )
               },
              handleCurrentChange(currentRow,oldCurrentRow){
                this.handledSelected = true;
                console.log ("*********** handledSelected",  this.handledSelected )
                this.lastSelected = this.$store.state.titleRequest.selectedRequest;
               
                console.log (" pre selected row", currentRow )
                if (currentRow === null) {
                    console.log("row null")
                    // this.lastSelected = this.$store.state.titleRequest.selectedRequest;
                }
                else {
                  if (oldCurrentRow !== null) {
                      this.lastSelected = oldCurrentRow;
                  }
    
                    console.log("post selected row", currentRow, oldCurrentRow)
                    if (this.$store.state.titleRequest.selectedChange) {
                      this.dialogVisible = true;
                    }
                }
              },
             
              
        },
        mixins: [
                  RequestHelperMixin,
            ],
           
            created() {
              console.log('request grid created');
              // var array = this.filteredRequests;
              //         if (array.length > 0) {
              //         // this.setCurrent(  array[0]);
              //         console.log('rrow key',this.getRowKeys (array[0]) );  
              //         }
                       
             this.handledSelected = false;
            
            }
          }
         // background: rgb(201, 245, 156);
    </script>
    <style>
       .el-table .warning-row {
        background: rgb(201, 245, 156);
      }
      /* .warning-row {
        background: rgb(201, 245, 156);
      }
      */
      /* .el-table__row:hover {
        background: rgb(201, 245, 156);
      } */
     
      /* .el-table tr:hover, 
      .el-table th:hover,
      .cell  {
       background: rgb(245, 156, 183);
    } */
      
      .el-table .success-row {
        background: #0c1d01;
      }
      /* tr:hover {
        background: #0c1d01;
      } */
    
      
      /* .el-table__row :hover
      {
        background: rgb(156, 162, 245);
      } */
      /* .el-input__inner{
        background: rgb(241, 245, 237);
      } */
     
    .el-row {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      .el-col {
        border-radius: 4px;
      }
      .bg-purple-dark {
        background: #99a9bf;
      }
      .bg-purple {
        background: #d3dce6;
      }
      .bg-purple-light {
        background: #e5e9f2;
      }
      .grid-content {
        border-radius: 4px;
        min-height: 36px;
      }
      .row-bg {
        padding: 10px 0;
        background-color: #f9fafc;
      }
    </style>


